@import url(https://fonts.googleapis.com/css?family=Lato:700&display=swap);
.dropdown-menu{background-color:#017aff}.dropdown-item{color:#fff;text-align:center}.btn-toolbar{margin:20px 20px 0px 20px}

.slider{margin-top:20px;color:#fff;width:200px}.slider p{position:absolute;top:-10px}

.array-bars{position:absolute;display:flex;justify-content:center;align-items:flex-end;bottom:0px;width:100%;z-index:-100;color:#fff}.array-bars div{width:10px;margin:0 1.5px}.transition{-webkit-transition:0.5s;transition:0.5s}

.App{text-align:center}html,body{background-color:#2f2f2f;font-family:'Lato', sans-serif}

